.c-button {
  color: #29415f;
  background: transparent;
  font-size: 16px;
  width: 200px;
  height: 40px;
  line-height: 40px;
  text-transform: uppercase;
}

.c-button {
  font-family: "Julius Sans One", sans-serif;
  text-align: center;
  border: 1px solid #29415f;
  position: relative;
  box-sizing: content-box;
}

.c-button::after {
  content: "";
  width: 22.5%;
  height: 1px;
  background-color: #29415f;
  position: absolute;
  top: 50%;
  right: -7.5%;
  z-index: 1;
}

.c-button__tag {
  position: relative;
  z-index: 1;
  display: block;
  overflow: hidden;
  transition: 0.3s;
}

.c-button__tag::before,
.c-button__tag::after {
  content: "";
  width: 100%;
  position: absolute;
  top: 0;
  z-index: -1;
  transition: transform ease 0.3s;
  box-sizing: content-box;
}

.c-button__tag::before {
  right: -60px;
  border-right: 60px solid transparent;
  border-bottom: 60px solid #cfab6e;
  transform: translateX(-100%);
}

.c-button__tag::after {
  left: -60px;
  border-left: 60px solid transparent;
  border-top: 60px solid #cfab6e;
  transform: translateX(100%);
}

.c-button__tag:hover {
  color: #fff;
}

.c-button__tag:hover::before {
  transform: translateX(-49%);
}

.c-button__tag:hover::after {
  transform: translateX(49%);
}

.c-card {
  width: 100%;
  aspect-ratio: 219/115.9;
  background-image: linear-gradient(90deg, transparent 0% 25%, #29415f 25% 100%);
  position: relative;
}
.c-card--mirror {
  background-image: linear-gradient(270deg, transparent 0% 25%, #29415f 25% 100%);
}

.c-card__box {
  display: block;
  overflow: hidden;
  width: 70%;
  height: auto;
  margin-top: 4.26%;
}
.c-card__box--mirror {
  margin-right: 0;
  margin-left: auto;
}

.c-card__image {
  width: 100%;
  height: auto;
  display: block;
  transition-duration: 0.5s;
}
.c-card__image:hover {
  transform: scale(1.2);
}

.c-card__title {
  color: #fff;
  margin-bottom: 0.15em;
  margin-right: 0.5em;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  position: absolute;
  bottom: 0;
  right: 0;
}
.c-card__title--mirror {
  left: 0;
  right: inherit;
  margin-left: 0.5em;
}

.c-beforeIconBtn {
  position: relative;
  display: block;
  padding: 0 0.1em 0.1em;
  border: 1px solid currentColor;
  color: #fff;
  text-decoration: none;
  transition: 0.25s;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.c-beforeIconBtn img {
  display: inline-block;
  margin-inline: 0.1em;
}

.c-check {
  width: 0.4em;
  height: 0.4em;
  border-bottom: 1px solid #fff;
  border-right: 1px solid #fff;
  transform: rotate(45deg);
  margin-inline: 0.2em;
  pointer-events: none;
}

.c-beforeIconBtn:focus,
.c-beforeIconBtn:hover {
  background-color: #fff;
  border-color: #29415f;
  color: #29415f;
}
.c-beforeIconBtn:focus img,
.c-beforeIconBtn:hover img {
  filter: brightness(0) saturate(100%) invert(21%) sepia(16%) saturate(1726%) hue-rotate(173deg) brightness(94%) contrast(87%);
}
.c-beforeIconBtn:focus .c-check,
.c-beforeIconBtn:hover .c-check {
  border-color: #29415f;
}

.c-language {
  display: flex;
  flex-direction: column;
}

.c-language__lists {
  transform: scaleY(0); /*デフォルトでは非表示の状態にしておく*/
  transform-origin: center top; /*変形を適応する基準をtopとする*/
  transition: all 0.3s; /*表示の変化を0.3秒に指定*/
  width: 100%;
}

.c-gnavi__list:hover .c-language__lists {
  transform: scaleY(1); /*Gナビメニューにホバーしたら表示*/
}

.c-language__list {
  background-color: #29415f;
  height: 25px;
  line-height: 25px;
  text-align: right;
  transition: all 0.3s;
  position: relative;
}

.c-language__list:not(:first-child)::after {
  content: "";
  width: 100%;
  height: 1px;
  background-color: #3492d1;
  position: absolute;
  top: 0;
  left: 0;
}

.c-language__list:hover {
  background-color: #2e648a;
}

.c-language__list a {
  color: #fff;
  display: block;
  padding-right: 1em;
  text-decoration: none;
  position: relative;
}

.c-language__list a::before {
  content: "";
  background-color: #fff;
  position: absolute;
  top: 11px;
  left: 10px;
  width: 7px;
  height: 5px;
  clip-path: polygon(0 0, 20% 50%, 0% 100%, 100% 50%);
}

