  top: 0;
  right: 0;
  z-index: 100;
}

.menu_inner {
  width: 100%;
  height: 100%;
  text-align: center;
  position: absolute;
  top: 0;
  right: -100%;
}

.menu_bg {
  width: 110%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 10%;
  background: rgba(163, 180, 190, 0.99);
  transform: skew(15deg);
}

.menu_block {
  display: inline-block;
  width: auto;
  height: auto;
  position: relative;
  top: 50%;
  left: -10px;
  transform: scale(1.1);
}

.menu_list {
  display: block;
  width: auto;
  height: auto;
  text-align: left;
  position: relative;
  transform: translate(-50px, 0);
}

.menu_list > div:nth-child(1) {
  height: auto;
  float: left;
}

.menu_list > div:nth-child(2) {
  width: auto;
  height: auto;
  float: left;
  position: relative;
}

.menu_list > div:nth-child(1) p {
  color: #fff;
  font-weight: 600;
