@use "global" as *;


.header_button {
  display: block;
  width: 100%;
  height: 100%;
  cursor: pointer;
  position: relative
}

.header_button_bg {
  width: 100%;
  height: 100%;
  background: $blue;
  position: absolute;
  top: 0;
  left: 0
}

.header_button_inner {
  width: 20px;
  height: 20px;
  margin: -10px 0 0 -10px;
  position: absolute;
  top: 50%;
  left: 50%;
  transition-duration: 0.25s;
  transition-delay: 0.25s
}

.header_button_inner div {
  width: 100%;
  height: 2px;
  position: absolute;
  left: 0;
  transition-duration: 0.25s;
  transition-delay: 0.25s
}

.header_button_inner>div:nth-child(1) {
  top: 0
}

.header_button_inner>div:nth-child(2) {
  top: 50%;
  margin: -1px 0 0 0
}

.header_button_inner>div:nth-child(3) {
  top: 100%;
  margin: -2px 0 0 0
}

.header_button_inner div span {
  width: 2px;
  height: 100%;
  position: absolute;
  top: 0;
  background: #fff;
  transition-duration: 0.5s;
  transition-delay: 0.25s
}

.header_button_inner div>span:nth-child(1) {
  left: 0
}

.header_button_inner div>span:nth-child(2) {
  left: 50%;
  margin: 0 0 0 -1px
}

.header_button_inner div>span:nth-child(3) {
  right: 0
}

.header_button_on .header_button_inner {
  width: 28px;
  height: 28px;
  margin: -14px 0 0 -14px;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1000;
}

.header_button_on .header_button_inner div {
  height: 1px;
  margin: 0;
  top: 50%
}

.header_button_on .header_button_inner>div:nth-child(1) {
  transform: rotate(-45deg)
}

.header_button_on .header_button_inner>div:nth-child(2) {
  opacity: 0
}

.header_button_on .header_button_inner>div:nth-child(3) {
  transform: rotate(45deg)
}

.header_button_on .header_button_inner div>span:nth-child(1) {
  width: 100%
}

.header_button_on .header_button_inner div>span:nth-child(2) {
  opacity: 0
}

.header_button_on .header_button_inner div>span:nth-child(3) {
  opacity: 0
}

