.menu {
  width: 33.333332%;
  width: -webkit-calc(100%/3);
  width: calc(100%/3);
  max-width: 500px;
  min-width: 400px;
  height: 100%;
  min-height: 600px;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 100
}

.menu_inner {
  width: 100%;
  height: 100%;
  text-align: center;
  position: absolute;
  top: 0;
  right: -100%
}

.menu_bg {
  width: 110%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 10%;
  background: rgba(163,180,190,.99);
  transform: skew(15deg)
}

.menu_block {
  display: inline-block;
  width: auto;
  height: auto;
  position: relative;
  top: 50%;
  left: -10px;
  transform: scale(1.1)
}

.menu_list {
  display: block;
  width: auto;
  height: auto;
  text-align: left;
  position: relative;
  transform: translate(-50px,0)
}

.menu_list>div:nth-child(1) {
  height: auto;
  float: left
}

.menu_list>div:nth-child(2) {
  width: auto;
  height: auto;
  float: left;
  position: relative
}

.menu_list>div:nth-child(1) p {
  color: #fff;
  // font-family: 'Lato','游ゴシック','Yu Gothic',YuGothic,'Hiragino Kaku Gothic ProN','Hiragino Kaku Gothic Pro','メイリオ',Meiryo,'ＭＳ ゴシック',sans-serif;
  font-weight: 600;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale
}

.menu_list>div:nth-child(2) p {
  letter-spacing: 1px;
  color: #fff
}

.menu_list>div:nth-child(2)>p:nth-child(1) {
  opacity: .25
}

.menu_list>div:nth-child(2)>p:nth-child(2),.menu_list>div:nth-child(2)>p:nth-child(3) {
  height: 100%;
  position: absolute;
  top: 0;
  overflow: hidden
}

.menu_list>div:nth-child(2)>p:nth-child(2) {
  width: 0;
  left: 0
}

.menu_list_off>div:nth-child(2)>p:nth-child(2) {
  width: 0
}

.menu_list>div:nth-child(2)>p:nth-child(3) {
  width: 0;
  right: 0
}

.menu_list_off>div:nth-child(2)>p:nth-child(3) {
  width: 100%
}

.menu_list>div:nth-child(2)>p:nth-child(3) span {
  width: 120px;
  height: auto;
  position: absolute;
  top: 0;
  right: 0;
  text-align: right
}

