.header_button_fix .header_button_bg {
  animation: header_button_fix 0.25s linear forwards
}

@keyframes header_button_fix {
  0% {
      opacity: 0
  }

  100% {
      opacity: 1
  }
}

.header_button_nofix .header_button_bg {
  animation: header_button_nofix 0.25s linear forwards
}

@keyframes header_button_nofix {
  0% {
      opacity: 1
  }

  100% {
      opacity: 0
  }
}

.header_button_pause .header_button_bg {
  animation: header_button_pause 0.25s linear forwards
}

@keyframes header_button_pause {
  0% {
      opacity: 1
  }

  100% {
      opacity: 0
  }
}


.menu_list_on>div:nth-child(2)>p:nth-child(2) {
  animation: menu_list_on 0.25s ease-in-out forwards
}

@keyframes menu_list_on {
  0% {
      width: 0%
  }

  100% {
      width: 100%
  }
}

.menu_list_off>div:nth-child(2)>p:nth-child(3) {
  animation: menu_list_off 0.25s ease-in-out forwards
}

@keyframes menu_list_off {
  0% {
      width: 100%
  }

  100% {
      width: 0%
  }
}



.menu_on .menu_inner {
  animation: menu_on0 0.5s ease-in-out forwards
}

@keyframes menu_on0 {
  0% {
      right: -100%;
      opacity: 0
  }

  100% {
      right: 0%;
      opacity: 1
  }
}

.menu_on .menu_bg {
  animation: menu_on1 0.5s ease-in-out forwards
}

@keyframes menu_on1 {
  0% {
      left: 10%;
      transform: skew(15deg)
  }

  100% {
      left: 0%;
      transform: skew(0deg)
  }
}

.menu_on .menu_block {
  animation: menu_on2 0.5s ease-in-out 0.25s forwards
}

@keyframes menu_on2 {
  0% {
      transform: scale(1.1)
  }

  100% {
      transform: scale(1)
  }
}

.menu_on .menu_block>.menu_list:nth-child(1) {
  animation: menu_on3 0.5s ease-in-out 0.25s forwards
}

.menu_on .menu_block>.menu_list:nth-child(2) {
  animation: menu_on3 0.5s ease-in-out 0.3s forwards
}

.menu_on .menu_block>.menu_list:nth-child(3) {
  animation: menu_on3 0.5s ease-in-out 0.35s forwards
}

.menu_on .menu_block>.menu_list:nth-child(4) {
  animation: menu_on3 0.5s ease-in-out 0.4s forwards
}

.menu_on .menu_block>.menu_list:nth-child(5) {
  animation: menu_on3 0.5s ease-in-out 0.45s forwards
}

.menu_on .menu_block>.menu_list:nth-child(6) {
  animation: menu_on3 0.5s ease-in-out 0.5s forwards
}

.menu_on .menu_block>.menu_list:nth-child(7) {
  animation: menu_on3 0.5s ease-in-out 0.55s forwards
}

.menu_on .menu_block>.menu_list:nth-child(8) {
  animation: menu_on3 0.5s ease-in-out 0.6s forwards
}

.menu_on .menu_block>.menu_list:nth-child(9) {
  animation: menu_on3 0.5s ease-in-out 0.65s forwards
}

@keyframes menu_on3 {
  0% {
      transform: translate(-50px,0);
      opacity: 0
  }

  100% {
      transform: translate(0,0);
      opacity: 1
  }
}

.menu_on .menu_sns {
  animation: menu_on4 0.5s ease-in-out 0.7s forwards
}

@keyframes menu_on4 {
  0% {
      opacity: 0
  }

  100% {
      opacity: 1
  }
}

.menu_off .menu_inner {
  animation: menu_off0 0.5s ease-in-out forwards
}

@keyframes menu_off0 {
  0% {
      right: 0%;
      opacity: 1
  }

  100% {
      right: -100%;
      opacity: 0
  }
}

.menu_off .menu_bg {
  animation: menu_off1 1s ease-in-out forwards
}

@keyframes menu_off1 {
  0% {
      left: 0%;
      transform: skew(0deg)
  }

  100% {
      left: 10%;
      transform: skew(-15deg)
  }
}

.menu_off .menu_block {
  animation: menu_off2 0.5s ease-in-out forwards
}

@keyframes menu_off2 {
  0% {
      transform: scale(1)
  }

  100% {
      transform: scale(1)
  }
}

.menu_off .menu_list {
  animation: menu_off3 0.5s ease-in-out forwards
}

@keyframes menu_off3 {
  0% {
      transform: translate(0,0);
      opacity: 1
  }

  100% {
      transform: translate(0,0);
      opacity: 0
  }
}

.menu_off .menu_sns {
  animation: menu_off4 0.5s ease-in-out forwards
}

@keyframes menu_off4 {
  0% {
      opacity: 1
  }

  100% {
      opacity: 0
  }
}
